<template>
  <div>
    <a-card style="background-color:#fff8db;margin-bottom:10px" v-show="show">
      <div class="table-page-search-wrapper">
        <a-form class="ant-advanced-search-form searchFixed" >
          <a-row :gutter="32">
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;">
              <a-form-item :label="$t('dept.index')" >
                <dept-select ref="dept" @change="(value)=>{value?queryParam.deptId=value:delete queryParam.deptId; this.queryData()}"/>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;">
              <a-form-item :label="this.$t('vee.datatype')">
                <a-select v-model="datetype">
                  <a-select-option value="0">
                    {{ $t('dct.REPEAT_TYPE3') }}
                  </a-select-option>
                  <a-select-option value="1">
                    {{ $t('dct.REPEAT_TYPE4') }}
                  </a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;" v-show="datetype=='0'">
              <a-form-item :label="$t('common.time')" >
                <pick-date :defaultValue="nowTime" @change="onChangeTime" :allowClear="false"></pick-date>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;" v-show="datetype=='1'">
              <a-form-item :label="$t('common.time')" >
                <pick-month :defaultValue="nowTime1" @change="onChangeTime" :allowClear="false"></pick-month>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:30px;">
              <a-form-item >
                <span>
                  <btn-search @click="queryData" :showInput="false"></btn-search>&nbsp; &nbsp;
                  <a-button @click="reCount" type="primary" ghost>{{ $t('common.recount') }}</a-button>&nbsp; &nbsp;
                  <btn-export @click="exportData" :showInput="false"></btn-export>
                </span>
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </div>
    </a-card>
    <a-card>
      <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
        <a-row>
          <a-col :md="19" :sm="19">
          </a-col>
          <a-col :md="5" :sm="5">
            <sorter @refresh="$refs.table.refresh()" @show="showSearch"></sorter>
          </a-col>
        </a-row>
      </div>
      <div style="width:50%;float:left">
        <v-chart :forceFit="true" :height="height" :data="data" :scale="scale">
          <v-tooltip :showTitle="false" dataKey="item*percent" />
          <v-axis />
          <v-legend dataKey="item" />
          <v-pie position="percent" color="item" :v-style="pieStyle" :label="labelConfig" />
          <v-coord type="theta" />
        </v-chart>
      </div>
      <div style="width:50%;float:left">
        <v-chart :forceFit="true" :height="height" :data="data1" :scale="scale">
          <v-tooltip :showTitle="false" dataKey="item*percent" />
          <v-axis />
          <v-legend dataKey="item" />
          <v-pie position="percent" color="item" :v-style="pieStyle" :label="labelConfig" />
          <v-coord type="theta" />
        </v-chart>
      </div>
      <s-table 
        ref="table" 
        rowKey="deviceId,nowTime"
        :data="loadData" 
        :bordered="false" 
      > 
        <a-table-column 
          :title="$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column
          :title="$t('line.name')"
          width="150px"
          dataIndex="refContent"
        >
        </a-table-column>
        <a-table-column
          :title="$t('line.code')"
          width="150px"
          dataIndex="refId"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('power.index2')"
          width="150px"
          dataIndex="totalKwh"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('power.index9')"
          width="150px"
          dataIndex="priLinePriTmKwh"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('power.index10')"
          width="150px"
          dataIndex="pubLinePriTmKwh"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('power.index11')"
          width="150px"
          dataIndex="pubLinePubTmKwh"
        >
        </a-table-column>
      </s-table>
    </a-card>
  </div>
</template>
<script>
  import STable from '@/components/table/'
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import { toUTC } from '@/inhe/picker/utils'
  import DeptSelect from '@/views/public/DeptSelect'

  const DataSet = require('@antv/data-set')
  export default {
    name: 'Index',
    components: {
      STable,
      Dict,
      DeptSelect,
    },
    data() {
      return {
        show:true,
        queryParam: {},
        datetype:'0',
        data:[],
        data1:[],
        scale :[{
          dataKey: 'percent',
          min: 0,
          formatter: '.0%',
        }],
        pieStyle: {
          stroke: '#fff',
          lineWidth: 1
        },
        labelConfig: ['percent', {
          formatter: (val, item) => {
            return item.point.item + ': ' + val
          }
        }],
        height:'250',
        nowTime1:toUTC(new Date(new Date().getTime()-30*24*3600*1000)),
        nowTime: toUTC(new Date(new Date().getTime()-24*3600*1000)),
        loadData:  parameter => { 
          const that = this
          if(that.datetype=='0'){
            parameter.nowTime = that.nowTime.slice(0,10)
          }else{
            parameter.nowTime = that.nowTime1.slice(0,7)
          }
          parameter.way = 1
          parameter.dataType = that.datetype    
          return axios({
            url: '/analysis/consumption/structure/search',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => { 
            this.getData(res.result.data)          
            return res.result
          })
        },

      }
    },
    methods: {
      getData(record){
        const that=this
        that.data=[]
        that.data1=[]
        var sourceData=[]
        var sourceData1=[]
        const total=[0,0,0]
        record.forEach(item => {
          sourceData.push({
           item:item.refContent,
           count:item.totalKwh
          })
          total[0]+=item.priLinePriTmKwh
          total[1]+=item.pubLinePriTmKwh
          total[2]+=item.pubLinePubTmKwh
        })
        sourceData1=[
          { item: this.$t('power.index9'), count: total[0] },
          { item: this.$t('power.index10'), count: total[1] },
          { item: this.$t('power.index11'), count: total[2] },
        ]
        if(record.length==0){
          sourceData1=[]
        }
        const dv = new DataSet.View().source(sourceData)
        const dv1 = new DataSet.View().source(sourceData1)
        dv.transform({
          type: 'percent',
          field: 'count',
          dimension: 'item',
          as: 'percent'
        })
        dv1.transform({
          type: 'percent',
          field: 'count',
          dimension: 'item',
          as: 'percent'
        })
      that.data = dv.rows
      that.data1 = dv1.rows
      },
      onChangeTime(date){
        date=toUTC(new Date(new Date(date).getTime()+8*3600*1000))
         if(this.datetype=='0'){
           this.nowTime=date
         }else{
           this.nowTime1=date
         }
      },
      showSearch() {
        this.show = !this.show
      },
      exportData() {        
        const that = this
        const data = {
          isoCode: localStorage.getItem('locale')
        }  
        if(that.datetype=='0'){
          data.nowTime = that.nowTime.slice(0,10)
        }else{
          data.nowTime = that.nowTime1.slice(0,7)
        }
        data.way = 1
        data.dataType = that.datetype     
        axios({
          url: '/analysis/consumption/structure/export',
          method: 'get',
          params: Object.assign(data, this.queryParam),
          responseType: 'blob'
        }).then(res => {
          const url = window.URL.createObjectURL(new Blob([res]))
          const link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', this.$t('function._32')+'.xls')
          document.body.appendChild(link)
          link.click()          
        })
      },
      reCount(){
        axios({
          url: '/analysis/consumption/structure/update-reset',
          method: 'post',
          params: {taskCode:this.datetype=='0'?'004,006':'005,007',dataTime:this.datetype=='0'?this.nowTime.slice(0,10):this.nowTime1.slice(0,7)+'-01'},
        }).then(res => {
          if(res.result){
            this.$message.info('Succeeded!')
          }
            
        })
      },
      //输入任务名称的search,上方列表表刷新
      queryData(){
        this.$refs.table.refresh()
      }
    }
  }
</script>
<style lang="scss" scoped>
.headtab{
  background: #fff;
}
.ant-tabs>.ant-tabs-bar{
  margin: 0 !important;
}
.ant-form-item-children .ant-calendar-picker{
  width: 100%;
}
</style>